<template>
  <div>
    <h2>{{ Ciudad }}</h2>
    <h2>Fecha: {{ Fecha }}</h2>
    <p>Región: {{ Region }}</p>
    <p>País: {{ País }}</p>
    <p>Latitud: {{ Latitud }}</p>
    <p>Longitud: {{ Longitud }}</p>
    <p>Temperatura promedio: {{ TemperaturaPromedio }}°</p>
    <p>Temperatura máxima: {{ TemperaturaMáxima }}°C</p>
    <p>Temperatura mínima: {{ TemperaturaMínima }}°C</p>
    <p>Condición: {{ Condición }}</p>
  </div>
</template>

<script>
export default {
  props: {
    Fecha: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      Ciudad: '',
      Region: '',
      País: '',
      TemperaturaPromedio: 0,
      TemperaturaMáxima: 0,
      TemperaturaMínima: 0,
      Condición: '',
      Latitud: 0,
      Longitud: 0,
    };
  },
  mounted() {
    this.fetchClimaFuturo();
  },
  watch: {
    Fecha: {
      immediate: true,
      handler() {
        this.fetchClimaFuturo();
      },
    },
  },
  methods: {
    async fetchClimaFuturo() {
      try {
        const apiUrl = 'https://api.weatherapi.com/v1/future.json';
        const apiKey = '8a617536031e41558ac224116232206';
        const ubicación = 'Trujillo';
        const Fecha = this.Fecha;
  
        const response = await fetch(`${apiUrl}?key=${apiKey}&q=${ubicación}&lang=es&aqi=yes&dt=${Fecha}`);
        const data = await response.json();
  
        this.Ciudad = data.location.name;
        this.Region = data.location.region;
        this.País = data.location.country;
        this.TemperaturaPromedio = data.forecast.forecastday[0].day.avgtemp_c;
        this.TemperaturaMáxima = data.forecast.forecastday[0].day.maxtemp_c;
        this.TemperaturaMínima = data.forecast.forecastday[0].day.mintemp_c;
        this.Condición = data.forecast.forecastday[0].day.condition.text;
        this.Latitud = data.location.lat;
        this.Longitud = data.location.lon;
      } catch (error) {
        console.error(error);
      }
    },
  },
};
</script>
